.MainView {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-height: 100vh;
  min-width: 100vh;
  margin-left: 20px;
  margin-top: 5px;
}

.Title {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: flex-start;
  overflow: auto;
  padding: 20px;
}

.BattNum {
  display: inline-flex;
  margin-right: 25px;
}

.Back {
  border: 1px;
  border-radius: 2px;
  display: inline-flex;
  margin-right: 25px;
  color: blue;
  size: medium;
  cursor:pointer
}

.StartTime {
  display: inline-flex;
  margin-right: 25px;
}

.TabsBox {
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: flex-start;
  min-width: 100vh;
}

.SelectedTab {
  background-color: lightgreen;
  color: purple;
}

.Graphs {
  height: 70vh;
  width: 150vh;
  margin-top: 15px;
  border: 1px solid black;
  margin-left: 10px;
  margin-right: 10px;
}

.Graph {
  border: solid;
  border-color: white;
  border-width: 1px;
}